import * as React from 'react';
import { Box, Container, Grid2, Tab, Tabs, Typography } from "@mui/material";
import PropTypes from 'prop-types';
import '../../css/tenagaKependidikan.css';


function CustomTabPanel(props) {
    const {children, value, index, ...other} = props

    return (
        <div
            hidden={value !== index}
            id={`tab-panel-${index}`}
            aria-labelledby={`tab-${index}`}
            {...other}
        >
            {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
        </div>
    );
}

CustomTabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.number.isRequired,
    value: PropTypes.number.isRequired,
}

function a11yProps(index) {
    return {
      id: `simple-tab-${index}`,
      'aria-controls': `simple-tabpanel-${index}`,
    };
  }

function GuruSection(param) {
    const value = {'foto': {}}
    
    const person = {
      'Guru1': {
        'mapel': 'b.Indo',
        'foto': 'https://th.bing.com/th?id=OIP.A1CwO5Q3kquQPVeE9y4WWgHaJ9&w=215&h=289&c=8&rs=1&qlt=90&o=6&dpr=1.5&pid=3.1&rm=2'
      },
      'Guru2': {
          'mapel': 'b.Inggris',
          'foto': 'https://th.bing.com/th?id=OIP.48C1fjm5A4taM6KNeVPyTQHaKI&w=213&h=292&c=8&rs=1&qlt=90&o=6&dpr=1.5&pid=3.1&rm=2'
      },
      'Guru3': {
        'mapel': 'Matematika',
        'foto': 'https://th.bing.com/th/id/OIP.0kf38aaFxP8Di1oNWTpADQHaI_?w=150&h=183&c=7&r=0&o=5&dpr=1.5&pid=1.7',
      },
      'Guru4': {
        'mapel': 'b.Jawa',
        'foto': ''
      }
    }

    switch (param) {
        case 0:
            value.foto = person
            break;
        case 1:
            value.judul = 'Staff'
            break
        default:
            break;
    }

    function PelengkapFoto() {
      if (Object.keys(person).length % 3 === 1) {
        return (
          <>
            <Grid2 size={{xs: 12, md: 6, lg: 3.5,}} textAlign={'center'} margin={'auto'}>
            </Grid2>
            <Grid2 size={{xs: 12, md: 6, lg: 3.5,}} textAlign={'center'} margin={'auto'}>
            </Grid2>
          </>
        )
      }   else if (Object.keys(person).length % 3 === 2) {
        return (
          <>
            <Grid2 size={{xs: 12, md: 6, lg: 3.5,}} textAlign={'center'} margin={'auto'}>
            </Grid2>
          </>
        )
      }    
    }

    return (
        <Box maxWidth={1280} margin={'auto'}>
            <Grid2 container rowSpacing={3} columnSpacing={0} width={'auto'}>
                {Object.keys(value.foto).map((key) => (
                    <Grid2 size={{xs: 12, md: 6, lg: 3.5,}} sx={{margin: 'auto', position:'relative', overflow: 'hidden', display: 'flex', justifyContent: 'center'}}>
                        <img
                              src={value.foto[key].foto}
                              alt=''
                              loading='lazy'
                              width={300}
                              height={400}
                              className='zoom'
                        />
                        <Box sx={{position: 'absolute', bottom: 0, color: 'white', width: 300, zIndex: 1, bgcolor: 'rgba(0, 0, 0, 0.5)', textAlign: 'center', paddingTop: 1, paddingBottom: 2}}>
                          <Typography variant='body1'>{key}</Typography>
                          <Typography variant='body2'>{value.foto[key].mapel}</Typography>
                        </Box>
                    </Grid2>
                ))}
                {<PelengkapFoto />}
            </Grid2>
        </Box>
    )
}

export default function TenagaKependidikan() {
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
      setValue(newValue);
    };
  
    return (
      <Container maxWidth='xl'>
      <Box sx={{ width: '100%' }}>
        <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
            <Tab label="GURU" {...a11yProps(0)} />
            <Tab label="STAFF" {...a11yProps(1)} />
          </Tabs>
        </Box>
        <CustomTabPanel value={value} index={0}>
          {GuruSection(0)}
        </CustomTabPanel>
        <CustomTabPanel value={value} index={1}>
          {GuruSection(1)}
        </CustomTabPanel>
      </Box>
      </Container>
    );
}