import { Container, Box, Typography, Grid2, Button } from "@mui/material";
import EmailIcon from '@mui/icons-material/Email';
import InstagramIcon from '@mui/icons-material/Instagram';
import YouTubeIcon from '@mui/icons-material/YouTube';

export default function Footer() {
    return (
        <div>
            <Box sx={{ 
                py:2.5, 
                px: 2,
                mt:'auto',
                color: 'white',
                backgroundColor: (theme) => 
                    theme.palette.mode === 'light' ?
                    theme.palette.primary.light:
                    theme.palette.primary.dark
                }}>
                <Container maxWidth="xl">
                    <Grid2 container spacing={2}>
                        <Grid2 size={{xs: 12, md: 8}} sx={{ display: {xs: 'block', md: 'flex'}, flexDirection:'column', justifyContent: 'flex-start', gap: '10px'}}>
                            <Typography variant="h5">Tentang Kami</Typography>
                            <Typography variant="body1">Sekolah Kejuruan di Surabaya, Jawa Timur yang berlokasi di Jl. Simolawang Baru No.59 Surabaya, SMK PGRI 10 Surabaya bertekad mencapai perbaikan yang berkesinambungan berdasarkan sistem manajemen mutu ISO 9001:2008.
                            Telp. 0xx-xxxxxxx FAX. 031- 8292039, Email : info@pgri10-sby.sch.id</Typography>
                            <Box display={'flex'} gap={2}>
                                <Button variant="contained" href="https://www.instagram.com/" color="white" sx={{ backgroundColor: (e) => e.palette.primary.dark }}>
                                    <InstagramIcon />
                                </Button>
                                <Button variant="contained" href="https://youtube.com/" color="white" sx={{ backgroundColor: (e) => e.palette.primary.dark }}>
                                    <YouTubeIcon />
                                </Button>
                            </Box>
                        </Grid2>
                            <Grid2 size={{xs: 12 , md:4}}>
                            <iframe
                                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3958.0306438208645!2d112.75116047363466!3d-7.237344192768898!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x2dd7f966e87a2917%3A0xde23dbe69b703faf!2sSMK%20PGRI%2010%20SURABAYA!5e0!3m2!1sid!2sid!4v1729709117330!5m2!1sid!2sid"
                                width="100%"
                                height="300"
                                style={{ border: 0 }}
                                allowFullScreen=""
                                loading="lazy"
                                title="Peta Lokasi SMK PGRI 10 Surabaya"
                            ></iframe>
                            </Grid2>
                        </Grid2>
                </Container>
            </Box>

            <Box
            component="footer"
            sx={{
                py: 1,
                px: 2,
                mt: 'auto',
                xs: 'block',
                backgroundColor: (theme) =>
                    theme.palette.mode === 'light'
                ? theme.palette.grey[200]
                : theme.palette.grey[800],
            }}
            >
                <Container maxWidth="lg" sx={{justifyContent: 'space-between', display: {xs: 'block', sm: 'flex'}, textAlign: 'center'}}>
                    <Box alignItems={'center'} display={'flex'} gap={1} justifyContent={'center'} sx={{paddingBottom: {xs: '10px', sm: '0px'}}} >
                        <EmailIcon fontSize="small"/>
                        <Typography variant="body2">
                            www.smknpgri1-sby.co.id
                        </Typography>
                    </Box>
                    <Typography variant="body2">
                        &copy; {new Date().getFullYear()} Your Website. All rights reserved.
                    </Typography>
                </Container>
            </Box>
        </div>
    )
}