import React from "react";
import { Box, Button, Grid2, Typography } from "@mui/material"
import TipsAndUpdatesOutlinedIcon from '@mui/icons-material/TipsAndUpdatesOutlined';
import SchoolOutlinedIcon from '@mui/icons-material/SchoolOutlined';

export default function VisiDanMisi() {
    const [getVisiOrMisi ,setVsiOrMisi] = React.useState('visi');

    let content;

    if (getVisiOrMisi === 'visi') {
        content = 
        <Box display={{ xs: 'block', md: 'flex' }} gap={3} height={'100%'}>
            <Box
                component="img"
                src="https://upload.wikimedia.org/wikipedia/commons/thumb/2/20/BrightonHighSchoolNewYorkFront.jpg/1200px-BrightonHighSchoolNewYorkFront.jpg"
                alt="Visi"
                width={{ xs: '100%', md: '300px' }}
                height="auto"
                sx={{ mb: { xs: 2, md: 0 } }}
            />
            <Box>
                <Typography variant="h4" fontWeight="bold" mb={2}>
                    Visi
                </Typography>
                <Typography variant="body1">
                    TERWUJUDNYA PESERTA DIDIK YANG UNGGUL DALAM PRESTASI, INOVATIF, TERAMPIL, MANDIRI, BERBUDI LUHUR, DAN BERWAWASAN LINGKUNGAN
                </Typography>
            </Box>
        </Box>
    } else if (getVisiOrMisi === 'misi') {
        content =
        <Box display={{ xs: 'block', md: 'flex' }} gap={3} height={'100%'}>
            <Box
                component="img"
                src="https://upload.wikimedia.org/wikipedia/commons/thumb/2/20/BrightonHighSchoolNewYorkFront.jpg/1200px-BrightonHighSchoolNewYorkFront.jpg"
                alt="Misi"
                width={{ xs: '100%', md: '300px' }}
                height="auto"
                sx={{ mb: { xs: 2, md: 0 } }}
            />
            <Box>
                <Typography variant="h4" fontWeight="bold" mb={2}>
                    Misi
                </Typography>
                <Typography variant="body1">
                    Mengoptimalkan proses pembelajaran dan bimbingan serta kerjasama dengan dunia usaha dan dunia industri. Mengembangkan bidang ilmu pengetahuan dan teknologi berdasarkan minat, bakat dan potensi peserta didik. Mengembangkan bidang ilmu pengetahuan dan teknologi berdasarkan minat, bakat dan potensi.
                </Typography>
            </Box>
        </Box>
    }

    return (
        <Box height={{md: '80vh', xs: 'auto'}}>
            <Grid2 container spacing={0} height={'100%'} sx={{ display: 'flex', alignItems: 'center' }}>
                <Grid2 size={{md: 3, xs: 12}} sx={{ display: 'flex', flexDirection: {xs: 'row', md: 'column'}, justifyContent: 'center', gap: 2, alignItems: 'center' }}>
                    <Button variant="contained" sx={{ p: 4, width: '50%' }} color="inherit" onClick={() => setVsiOrMisi('visi')}><TipsAndUpdatesOutlinedIcon /> Visi</Button>
                    <Button variant="contained" sx={{ p: 4, width: '50%' }} color="inherit" onClick={() => setVsiOrMisi('misi')}><SchoolOutlinedIcon /> Misi</Button>
                </Grid2>
                <Box sx={{ width: '1px', height: '200px', backgroundColor: 'black', mx: 4, display: {xs: 'none', md: 'block'} }} />
                <Grid2 size={{md: 7, xs: 12}}>
                    <Box  height={{md: '250px', xs: 'auto'}} sx={{ backgroundColor: '#F5F7F8', boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.2)' }} p={6}>
                        {content}
                    </Box>
                </Grid2>
            </Grid2>
        </Box>
    )
}