import React from 'react';
import { AppBar, Toolbar, Typography, Box, Menu, MenuItem, Button, Container, Divider, IconButton, Drawer, List, ListItem } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';

const profiles = ['Visi & Misi', 'Struktur Organisasi', 'Tenaga Kependidikan', 'Sarana & Prasarana', 'Teaching Factory', 'Galeri Foto'];
const jurusan = ['Multimedia (MM)', 'Akutansi Lembaga (AKL)', 'Otomatisasi dan Tata Kelola Perkantoran (OTKP)'];
const informasi = ['Mr. iso', 'Pengumuman'];
const program = ['Lembaga Sertifikasi Profesi', 'Bursa Kerja Khusus (BKK)']
const navbarItems = {
  'Home': ['home'],
  'Profil': profiles,
  'Jurusan': jurusan,
  'Informasi': informasi,
  'Berita': ['Berita'],
  'Modul': ['Modul'],
  'Program': program,
};

export default function MyNavbar() {
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [activeMenu, setActiveMenu] = React.useState(null);
  const [open, setOpen] = React.useState(false);

  const handleOpenProfileMenu = (event, key) => {
    if (activeMenu === key) {
      setActiveMenu(null);
      setAnchorEl(null);
    } else {
      setActiveMenu(key);
      setAnchorEl(event.currentTarget);
    }
  };

  const handleCloseProfileMenu = () => {
    setActiveMenu(null);
    setAnchorEl(null);
  };

  const toggleDrawer = (newOpen) => () => {
    setOpen(newOpen);
  };

  function hrefDrawer (key, item) {
    if (navbarItems[key].length > 1) {
      return (
        <Button
          sx={{ textAlign: 'center', fontSize: '12px', width: '100%'}}
          href={`/#/${key.toLowerCase()}/${item.replace(/\s+/g, '-').toLowerCase()}`}
          >
            {item}
          </Button>
      )
    } else {
      return (
      <Button
          sx={{ textAlign: 'center', fontSize: '12px', width: '100%'}}
          href={`/#/${key.toLowerCase()}`}
          >
            {item}
          </Button>
      )
    }
  }

  const drawerContent = (
    <Box sx={{ width: 250 }} role="presentation" onClick={toggleDrawer(false)}>
      <List>
        {Object.keys(navbarItems).map((key) => (
          <div key={key}>
            <ListItem>
              <Typography variant="h6" textAlign={'center'} width={'100%'}>{key}</Typography>
            </ListItem>
            {navbarItems[key].map((item) => (
              <ListItem key={item}>
                {hrefDrawer(key, item)}
              </ListItem>
            ))}
            <Divider />
          </div>
        ))}
      </List>
    </Box>
  );

  return (
    <AppBar position="static">
      <Container maxWidth="xl">
        <Toolbar>

          <Button display={'flex'} href='/'>
            <img src='https://smkpgri10sby.sch.id/wp-content/uploads/2021/11/Logo-SKAGRISA-PNG.png' alt='logo' width={'40px'}/>
            <Typography variant="h6" color='white'>
              SMK PGRI 10
            </Typography>
          </Button>

          <Box sx={{ fontWeight: '600', display: { xs: 'none', md: 'flex' }, marginLeft: 'auto' }}>
            
            {Object.keys(navbarItems).map((key) => (
              <Box key={key}>
                <Button
                  color="inherit"
                  onClick={navbarItems[key].length > 1 ? (e) => handleOpenProfileMenu(e, key) : () => window.location.href = `/${key}`}
                  sx={{ cursor: 'pointer', userSelect: 'none' }}
                >
                  {key}
                </Button>
                {navbarItems[key].length > 0 && activeMenu === key && (
                  <Menu
                    anchorEl={anchorEl}
                    anchorOrigin={{
                      vertical: 'bottom',
                      horizontal: 'left',
                    }}
                    keepMounted
                    transformOrigin={{
                      vertical: 'top',
                      horizontal: 'left',
                    }}
                    open={Boolean(anchorEl)}
                    onClose={handleCloseProfileMenu}
                  >
                    {navbarItems[key].map((profile) => (
                      <MenuItem key={profile} onClick={handleCloseProfileMenu}>
                        <Button
                          sx={{ textAlign: 'center', fontSize: '12px', my: 0.5, display: 'block' }}
                          href={`/#/${key.toLowerCase()}/${profile.replace(/\s+/g, '-').toLowerCase()}`}
                        >
                          {profile}
                      </Button>
                      </MenuItem>
                    ))}
                  </Menu>
                )}
              </Box>
            ))}
          </Box>
          <Box sx={{ display: { xs: 'flex', md: 'none', marginLeft: 'auto' } }}>
            <IconButton aria-label="Menu" onClick={toggleDrawer(true)}>
              <MenuIcon />
            </IconButton>

            <Drawer open={open} onClose={toggleDrawer(false)}>
              {drawerContent}
            </Drawer>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
